<template>
  <div v-clickoutside:outside="closebar">
    <div class="navBar">
      <div class="navB">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item v-for="(item, index) in path" :key="index"
            ><a @click="getPath(item, index)">{{ item }}</a>
          </el-breadcrumb-item>
          <el-breadcrumb-item>
            {{ nowfolder }}
          </el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <div class="view">
        <el-radio-group v-model="listType"
          ><span class="viewTip">视图：</span>
          <el-radio label="平铺"></el-radio>
          <el-radio label="列表"></el-radio>
        </el-radio-group>
      </div>
    </div>
    <el-table
      v-if="listType === '列表'"
      :data="tableData"
      :cell-style="{ 'text-align': 'center' }"
      :header-cell-style="{ 'text-align': 'center' }"
      style="width: 100%"
    >
      <el-table-column type="index"> </el-table-column>
      <el-table-column prop="file_name" label="文件名" width="180px">
      </el-table-column>
      <el-table-column
        prop="size"
        label="文件大小"
        width="180px"
        :formatter="dealSize"
      >
      </el-table-column>
      <el-table-column
        prop="upload_time"
        label="上传时间"
        width="180px"
        :formatter="dealTime"
      >
      </el-table-column>
      <el-table-column prop="download" label="下载次数" width="180px">
      </el-table-column>
      <el-table-column label="操作" min-width="200px">
        <template slot-scope="scope">
          <a :href="getFile(scope.row)" :download="scope.row.file_name">
            <el-button size="mini" type="success" @click="handleDownload"
              >下载</el-button
            >
          </a>
          <el-button
            size="mini"
            type="primary"
            @click="handleShare(scope.$index, scope.row)"
            >分享</el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div class="listpp" v-if="listType === '平铺'">
      <div class="infoBox outside">
        <div class="infoBar outside" v-if="barFlag === 1">
          <p class="fs outside" v-if="clickFile.type !== '.folder'">
            文件大小：
            {{ clickFile.filesize }}
          </p>
          <p class="ft outside">
            上传时间：
            {{ clickFile.upload_time }}
          </p>
          <p class="fd outside" v-if="clickFile.type !== '.folder'">
            下载次数：
            {{ clickFile.download }}
          </p>
          <div class="fb outside" v-if="clickFile.type !== '.folder'">
            <a :href="getFile(clickFile)" :download="clickFile.file_name">
              <el-button size="mini" type="success" @click="handleDownload"
                >下载</el-button
              >
            </a>
            <el-button
              size="mini"
              type="primary"
              @click="handleShare(clickFile.id, clickFile)"
              >分享</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(clickFile.id, clickFile)"
              >删除</el-button
            >
          </div>
        </div>
      </div>
      <div
        :class="{ fileListpp: true, fileListppClick: item.id === clickFile.id }"
        v-for="item in tableData"
        :key="item.id"
      >
        <div
          class="clickBox"
          @click="infoBar(item)"
          v-if="item.type !== '.folder'"
        >
          <div class="fileIcon outside">
            <img
              class="icon"
              width="256"
              height="256"
              :src="getIcon(item)"
              :alt="item.file_name"
            />
          </div>
          <p style="-webkit-box-orient: vertical" class="fileName">
            {{ item.file_name }}
          </p>
        </div>
        <div
          class="clickBox"
          @click="infoBar(item)"
          @dblclick="gotofolder(item)"
          v-if="item.type === '.folder'"
        >
          <div class="fileIcon outside">
            <img
              class="icon"
              width="256"
              height="256"
              :src="getIcon(item)"
              :alt="item.file_name"
            />
          </div>
          <p style="-webkit-box-orient: vertical" class="fileName">
            {{ item.file_name }}
          </p>
        </div>
      </div>
      <div class="fileListpp">
        <div class="clickBox" @click="showUpAlert()">
          <div class="fileIcon">
            <img
              class="icon"
              width="256"
              height="256"
              src="../assets/images/up.png"
              alt="上传文件"
            />
          </div>
          <p style="-webkit-box-orient: vertical" class="fileName">上传文件</p>
        </div>
      </div>
    </div>
    <fileUpload
      ref="upFile"
      :dialogVisible="ifShowUpload"
      @reGetFileList="reGetFileList"
    ></fileUpload>
  </div>
</template>  

<script>
import Clickoutside from "element-ui/src/utils/clickoutside";
import fileUpload from "./file-upload";
export default {
  directives: { Clickoutside },
  components: { fileUpload },
  data() {
    return {
      tableData: [],
      listType: "平铺",
      barFlag: 2,
      clickFile: {},
      path: [],
      nowfolder: "我的网盘",
      addfolderName: "",
      uid: 0,
      ifShowUpload: false,
      upPath: "",
    };
  },
  methods: {
    getIcon(item) {
      let type = item.type.replace(/./, "").toLowerCase();
      let iconPath = "";
      console.log(type);
      console.log(/^aep$|^ai$/.test(type));
      if (/^aep$|^ai$/.test(type)) {
        console.log(1);
        iconPath = "../assets/images/" + type + ".png";
      } else {
        console.log(2);
        iconPath = "../assets/images/unk.png";
      }
      return iconPath;
    },
    reGetFileList(path) {
      this.ifShowUpload = false;
      this.refreshFileList(path);
    },
    addfolder(addfolderName) {
      if (addfolderName !== "") {
        this.$http
          .post("/file/addfolder", addfolderName)
          .then((res) => {
            if (res.data.flag === 0) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.success(res.data.msg);
              let path = "";
              if (this.path.length === 0) {
                path = this.nowfolder;
              } else {
                path = this.path.join("/") + "/" + this.nowfolder;
              }
              this.$refs.upFile.setPath(path);
              this.refreshFileList(path);
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message.error("文件名禁止为空");
      }
    },
    showUpAlert() {
      this.$confirm("选择建立文件夹或上传文件", "提示", {
        distinguishCancelAndClose: true,
        confirmButtonText: "上传文件",
        cancelButtonText: "新建文件夹",
        type: "info",
        center: true,
      })
        .then(() => {
          this.ifShowUpload = true;
        })
        .catch((action) => {
          if (action !== "cancel") {
            return;
          } else {
            this.$prompt("请输入文件夹名称", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
              //inputErrorMessage: "邮箱格式不正确",
            })
              .then(({ value }) => {
                if (value) {
                  let upPath = "";
                  if (this.path.length === 0) {
                    upPath = this.nowfolder;
                  } else {
                    upPath = this.path.join("/") + "/" + this.nowfolder;
                  }
                  this.addfolder({
                    file_name: value,
                    path: upPath,
                    uid: this.uid,
                    hash_name: "folder" + new Date().getTime(),
                  });
                } else {
                  this.$message.error("文件名禁止为空");
                }
              })
              .catch(() => {});
          }
        });
    },
    getPath(item, index) {
      let num = this.path.length;
      this.path.splice(index, num - index);
      this.nowfolder = item;
      let path = "";
      if (this.path.length === 0) {
        path = this.nowfolder;
      } else {
        path = this.path.join("/") + "/" + this.nowfolder;
      }
      this.$refs.upFile.setPath(path);
      this.refreshFileList(path);
    },
    gotofolder(item) {
      this.path = item.path.split("/");
      this.nowfolder = item.file_name;
      let path = "";
      if (this.path.length === 0) {
        path = this.nowfolder;
      } else {
        path = this.path.join("/") + "/" + this.nowfolder;
      }
      this.$refs.upFile.setPath(path);
      this.refreshFileList(path);
    },
    closebar() {
      this.barFlag = 2;
      this.clickFile = {};
    },
    infoBar(item) {
      this.barFlag = 1;
      this.clickFile = item;
      this.clickFile.filesize = this.dealSize({ size: item.size * 1 });
      this.clickFile.downloadUrl = `http://accurate.uicp.net:8081/file/download/${item.id}/${item.hash_name}`;
    },
    handleDelete(index, row) {
      this.$http
        .delete(`/file/delete/${row.hash_name}/${row.id}`)
        .then((res) => {
          this.$message.success(res.data.msg);
          let path = "";
          if (this.path.length === 0) {
            path = this.nowfolder;
          } else {
            path = this.path.join("/") + "/" + this.nowfolder;
          }
          this.$refs.upFile.setPath(path);
          this.refreshFileList(path);
        })
        .catch((err) => {
          console.log("Error=>", err);
        });
    },
    handleShare(index, row) {
      let url = `http://accurate.uicp.net:8081/file/download/${row.id}/${row.hash_name}`;
      this.$alert(
        `<p style="word-wrap: break-word; word-break: break-all;">${url}</p>`,
        "复制以下链接分享给好友哦~",
        {
          dangerouslyUseHTMLString: true,
          confirmButtonText: "确定",
          callback: (action) => {
            // this.$message({
            //   type: "info",
            //   message: `action: ${action}`,
            // });
          },
        }
      );
    },
    handleDownload() {
      let path = "";
      if (this.path.length === 0) {
        path = this.nowfolder;
      } else {
        path = this.path.join("/") + "/" + this.nowfolder;
      }
      this.$refs.upFile.setPath(path);
      setTimeout(() => {
        this.refreshFileList(path);
      }, 1000);
    },
    refreshFileList(path) {
      this.getFileList(path);
    },
    getFileList(path) {
      let params = {
        uid: sessionStorage.getItem("uid"),
        path: path,
      };
      this.uid = sessionStorage.getItem("uid");
      this.$http
        .post("/file/list", params)
        .then((res) => {
          if (res.data.code === 0) {
            this.$message.error(res.data.msg);
          } else {
            this.tableData = res.data;
            console.log(this.tableData);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getFile(data) {
      let url = `http://accurate.uicp.net:8081/file/download/${data.id}/${data.hash_name}`;
      return url;
    },
    dealSize(row, column) {
      if (0 < row.size && row.size < 102.4) {
        return `${row.size}byte`;
      } else if (102.4 <= row.size && row.size < 1024 * 1024) {
        let kb = (row.size / 1024).toFixed(2);
        return `${kb}Kb`;
      } else if (1024 * 1024 <= row.size && row.size < 1024 * 1024 * 1024) {
        let mb = (row.size / 1024 / 1024).toFixed(2);
        return `${mb}Mb`;
      } else if (
        1024 * 1024 * 1024 <= row.size &&
        row.size < 1024 * 1024 * 1024 * 1024
      ) {
        let gb = (row.size / 1024 / 1024 / 1024).toFixed(2);
        return `${gb}Gb`;
      }
    },
    dealTime(row, column) {
      return this.formatTime(row.upload_time);
    },
    formatTime(value) {
      var date = new Date(value);
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? `0${date.getMonth() + 1}`
          : date.getMonth() + 1;
      var D = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      var h = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
      var m =
        date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
      var s =
        date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds();
      return `${Y}-${M}-${D} ${h}:${m}:${s}`;
    },
  },
  mounted() {
    this.getFileList("我的网盘");
    this.$refs.upFile.setPath("我的网盘");
  },
};
</script>
<style scoped>
.viewTip {
  display: inline-block;
  width: 100px;
  height: 25px;
  font-size: 15px;
  margin-right: -19px;
}
.el-button + .el-button {
  margin: 0;
}
.listpp {
  width: 100%;
  overflow: hidden;
}
.fileListpp {
  box-sizing: border-box;
  float: left;
  width: 158px;
  height: 200px;
  font-size: 16px;
  border: 1px solid rgba(0, 0, 0, 0);
}
.fileListpp .icon {
  margin: 18px 18px 0 18px;
  width: 120px;
  height: 120px;
}
.fileListpp:hover {
  box-sizing: border-box;
  background: #e0f4fc;
  border: 1px solid #98e6fc;
  border-radius: 10px;
}
.fileListppClick {
  box-sizing: border-box;
  background: #c7eefc;
  border: 1px solid #1ed0ff;
  border-radius: 10px;
}
.fileListppClick:hover {
  box-sizing: border-box;
  background: #c7eefc;
  border: 1px solid #1ed0ff;
  border-radius: 10px;
}
.fileIcon {
  width: 158px;
  height: 143px;
}
.clickBox .fileName {
  word-break: break-all;
  height: 44px;
  line-height: 22px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
}
.infoBox {
  width: 100%;
  height: 50px;
}
.infoBar {
  width: 100%;
  height: 50px;
}
.infoBar p {
  display: block;
  font-size: 20px;
  text-align: left;
  line-height: 50px;
  float: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.infoBar .ft {
  width: 380px;
}
.infoBar .fs {
  width: 270px;
}
.infoBar .fd {
  width: 140px;
}
.infoBar .fb {
  padding-top: 10px;
  width: 200px;
  float: left;
}
.navBar {
  box-sizing: border-box;
  padding-top: 10px;
  width: 100%;
  height: 50px;
}
.navBar .navB {
  box-sizing: border-box;
  width: 80%;
  float: left;
  height: 30px;
  padding: 8px 12px;
  box-shadow: 0 0 8px #063e8b inset;
  transition: 0.2s;

  border-radius: 7px;
}
.navBar .navB:hover {
  box-shadow: 0 0 8px #377ee3 inset;
}
.navBar .view {
  box-sizing: border-box;
  padding-top: 7px;
  width: 230px;
  float: right;
}
</style>



